.App {
    text-align: center;
}

.App-header {
    background-color: #000000;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    color: white;
}

div.header {
    top: 0px;
    left: 0px;
    z-index: 100;
    height: 75px;
    width: 100%;
    position: fixed;
}

div.content {
    top: 75px;
    margin: 10px;
}

/* Begin Navbar styling */
div.fadeInHeader {
    transition: all 0.5s cubic-bezier(.17,.67,.83,.67);
    height: 100px;
}

div.scrolled {
    transition: all 0.5s cubic-bezier(.17,.67,.83,.67);
    background: rgb(0, 53, 89);
    background: rgba(0, 53, 89, 0.75);
    height: 75px;
}

/* Navbar buttons */
div.tabs {
    top: 0px;
    opacity: 1;
    vertical-align: middle;
    justify-content: center;
    grid-template-columns: repeat(4, 1fr);
    display: flex;
    height: 75px;
}

a.tabButton {
    vertical-align: middle;
    height: 65px;
    margin: 5px 8%;
    padding: 0 15px 0 15px;
    text-decoration: none;
    color:white!important;
    font-size: 20px;
}
a.tabButton:hover {
    border-radius: 10px;
    background-color:#006daa;
    transition: all 0.25s cubic-bezier(.17,.67,.83,.67);
}
a.tabButton.active {
    border-radius: 10px;
    background-color: #006daa;
}
a.tabButton p {
    vertical-align: middle;
    white-space: nowrap;
}